@import url(https://fonts.googleapis.com/css?family=Patrick+Hand&display=swap);
@import url(https://fonts.googleapis.com/css?family=IM+Fell+French+Canon&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bellota&display=swap);
* {
 outline: none;
}
@font-face {
    font-family: "Guanine";
    src: url(/static/media/guanine.323f7186.ttf);
}

@font-face {
    font-family: "Archia";
    src: url(/static/media/archia-regular-webfont.44b223ad.ttf);
}

@font-face {
    font-family: "Calligrafiti";
    src: url(/static/media/Calligraffiti.7abc87bc.ttf);
}

.react-article-source img {
    display: block;
    margin-top: 16px;
    margin-bottom: 16px;
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    object-fit: cover;
    max-width: 100%;
    text-align: center;
    max-height: 360px;
}

.react-article-source p {
    overflow: hidden;
    overflow-wrap: break-word;
    word-wrap: break-word;
}


.react-article-source blockquote {
    font-family: 'Bellota', cursive;
    padding: 12px;
    font-size: 18px;
    font-weight: 500;
    position: relative;
    color: #cccccc;
}

.react-article-source blockquote::before {
    font-family: 'Guanine', cursive;
    content: "\2018\2018";
    position: absolute;
    font-size: 64px;
    font-weight: bold;
    top: 0px;
    left: 0px;
    padding: 0px;
    margin: 0px;
    color: #0000002f;
}

